<template>
  <div class="mainb" :style="mainbBackgroundStyle">
    <div class="container">
      <div v-if="!topImgUrl">
        <bongo-cat></bongo-cat>
      </div>
      <div v-else>
        <img :src="topImgUrl" style="width:100%">
      </div>
      <div class="typer-content">
        <span class="cut">
          <span v-for="(letter,index) in words" :key="index">{{letter}}</span>
          <span>{{cursor}}</span>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import request from "@/utils/request";
import BongoCat from "./bongoCat.vue";

export default {
  components: { BongoCat },
  data() {
    return {
      str: "",
      words: [],
      letters: [],
      cursor: "▁",
      time: 0,
      mainbBackgroundStyle: {
        background: "url('https://s3.ax1x.com/2020/12/21/rBpqn1.gif')",
      },
      topImgUrl: "",
    };
  },
  beforeCreate() {},
  created() {},
  computed: {},
  mounted() {
    this.initContent();
  },
  methods: {
    initContent() {
      setInterval(() => {
        if (this.letters.length == this.words.length) {
          this.cursor = this.cursor == "▁" ? "" : "▁";
        }
      }, 700);
      if (this.$route.params.mark) {
        request
          .get("app/bongo/" + this.$route.params.mark)
          .then((response) => {
            this.str = response.data.content;
            this.mainbBackgroundStyle.background = response.data.backgroundUrl
              ? "url('" + response.data.backgroundUrl + "'"
              : this.mainbBackgroundStyle.background;
            this.topImgUrl = response.data.topImgUrl;
            document.title = response.data.webPageTitle
              ? response.data.webPageTitle
              : document.title;
            this.begin();
          })
          .catch(function (error) {
            console.log(error);
          });
      } else {
        this.str =
          "很喜欢的一个词是纷至沓来，​所有人都愿你的美好一拥而至，而我只祝你眼眸有星辰心中有山海，然后‎不动​声色的走下去，在将来的某天对自己​说：看！我还是都做到啦～";
        this.begin();
      }
    },
    begin() {
      this.letters = this.str.split("");
      for (var i = 0; i < this.letters.length; i++) {
        this.time += 100 + Math.floor(Math.random() * 100);
        setTimeout(this.write(i), this.time);
      }
    },
    write(i) {
      return () => {
        this.words.push(this.letters[i]);
      };
    },
  },
};
</script>

<style>
.mainb {
  width: 100%;
  height: 100vh;
}
.container {
  margin: 0 auto;
  padding: 60px 40px;
  width: 100%;
}
@media (min-width: 768px) {
  .container {
    width: 600px;
  }
}
.typer-content {
  margin-top: 40px;
  font-weight: bold;
  font-size: 20px;
  letter-spacing: 1.5px;
}
.cut {
  color: #494a5f;
}
</style>
