import { createWebHashHistory, createRouter } from 'vue-router';
import Home from '../views/index.vue'
import Lovq from '../views/lovq/index.vue'
import Emoticon from '../views/emoticon/index.vue';
import BongoCat from '../views/bongoCat/index.vue';
import Management from '../views/bongoCat/management.vue';

import Test from '../views/test/test.vue';

// createWebHistory
const history = createWebHashHistory();
const router = createRouter({
    history,
    routes: [
        {
            path: '/',
            component: Home,
            meta: {
                title: '首页',
                keepAlive: false
            }
        }, {
            path: '/test',
            component: Test,
            meta: {
                title: '测试页面',
                keepAlive: false
            }
        }, {
            path: '/lovq',
            component: Lovq,
            meta: {
                title: '计时器页面',
                keepAlive: false
            }
        }, {
            path: '/emoticon',
            component: Emoticon,
            meta: {
                title: '记仇表情包生成器',
                keepAlive: false
            }
        }, {
            path: '/bongo',
            component: BongoCat,
            meta: {
                title: '打字的喵喵',
                keepAlive: false
            }
        }, {
            path: '/bongo/:mark',
            component: BongoCat,
            meta: {
                title: '打字的喵喵',
                keepAlive: false
            }
        }, {
            path: '/mbongo',
            component: Management,
            meta: {
                title: 'Bongo管理',
                keepAlive: false
            }
        },
    ]
});

export default router;