<template>
  <div class="main">
    <h1 class="jz">Bongo 配置页面</h1>
    <a-card style="width: 100%;">
      <a-form :model="form" :label-col="labelCol" :wrapper-col="wrapperCol">
        <a-form-item label="ID" hidden>
          <a-input v-model:value="form.id" />
        </a-form-item>
        <a-form-item label="标记">
          <a-input v-model:value="form.mark" placeholder="页面标记(用于下次编辑页面用)" />
        </a-form-item>
        <a-form-item label="标记密码" hidden>
          <a-input v-model:value="form.password" type="password" placeholder="标记的密码(可空)"></a-input>
        </a-form-item>
        <a-form-item label="页面标题">
          <a-input v-model:value="form.webPageTitle" placeholder="页面的标题(可空)" />
        </a-form-item>
        <a-form-item label="顶部图片Url">
          <a-input v-model:value="form.topImgUrl" placeholder="页面顶部的图片Url(可空)" />
        </a-form-item>
        <a-form-item label="背景图片Url">
          <a-input v-model:value="form.backgroundUrl" placeholder="页面背景的图片Url(可空)" />
        </a-form-item>
        <a-form-item label="内容">
          <a-textarea v-model:value="form.content" placeholder="页面要显示的内容" />
        </a-form-item>
        <a-form-item label="访问地址">
          <a-input v-model:value="url" placeholder="访问地址" />
        </a-form-item>
        <a-form-item :wrapper-col="{ span: 20, offset: 4 }">
          <a-button class="mbutton" type="primary" @click="onSubmit">保存页面</a-button>
          <a-button class="mbutton" type="primary" @click="onEdit">加载页面</a-button>
          <a-button class="mbutton" @click="onPreview">预览</a-button>
          <a-button class="mbutton" @click="onClearForm">清空</a-button>
        </a-form-item>
      </a-form>
    </a-card>
  </div>
</template>
<script>
import request from "@/utils/request";

const baseUrl = "http://47.105.149.144:28/wildrose/#/bongo/";

export default {
  data() {
    return {
      labelCol: { span: 4 },
      wrapperCol: { span: 20 },
      form: {
        id: null,
        mark: null,
        password: null,
        webPageTitle: null,
        topImgUrl: null,
        backgroundUrl: null,
        content: null,
      },
      url: null,
    };
  },
  methods: {
    onSubmit() {
      if (!this.form.id) {
        var fromCreate = {
          mark: this.form.mark,
          password: this.form.password,
          webPageTitle: this.form.webPageTitle,
          topImgUrl: this.form.topImgUrl,
          backgroundUrl: this.form.backgroundUrl,
          content: this.form.content,
        };
        request
          .post("app/bongo", fromCreate)
          .then((response) => {
            this.form = response.data;
            this.url = baseUrl + response.data.id;
            this.openNotificationWithIcon("success", "保存成功", "");
          })
          .catch((error) => {
            this.openNotificationWithIcon("error", "保存失败", error.message);
          });
      } else {
        request
          .put(`app/bongo/${this.form.id}`, this.form)
          .then((response) => {
            this.form = response.data;
            this.url = baseUrl + response.data.id;
            this.openNotificationWithIcon("success", "保存成功", "");
          })
          .catch((error) => {
            this.openNotificationWithIcon("error", "保存失败", error.message);
          });
      }
    },
    onEdit() {
      if (this.form.mark) {
        request
          .get(`app/bongo/mark?mark=${this.form.mark}`)
          .then((response) => {
            this.form = response.data;
            this.url = baseUrl + response.data.id;
            this.openNotificationWithIcon("success", "读取成功", "");
          })
          .catch((error) => {
            this.openNotificationWithIcon("error", "读取失败", error.message);
          });
      } else {
        this.openNotificationWithIcon("info", "输入不正确", "请输入标记名");
      }
    },
    onPreview() {
      if (this.url) {
        window.open(this.url);
      } else {
        this.openNotificationWithIcon("info", "访问失败", "未生成访问地址");
      }
    },
    onClearForm() {
      for (let key in this.form) {
        this.form[key] = null;
      }
      this.url = null;
    },
    openNotificationWithIcon(type, title, message) {
      this.$notification[type]({
        message: title,
        description: message,
      });
    },
  },
};
</script>

<style>
.main {
  margin: 0 auto;
  padding: 20px 0 0 0;
  width: 90%;
}

@media (min-width: 768px) {
  .main {
    margin: 0 auto;
    padding: 20px 0 0 0;
    width: 700px;
  }
}

.mbutton {
  margin: 5px;
}

.jz {
  text-align: center;
}
</style>
